import { RegistrationForm } from '../registration-form/RegistrationForm';
import { LoginForm } from '../login-form/LoginForm';
import './AuthorizationModal.scss';
import { useDispatch, useSelector } from 'react-redux';
import { setActiveTab } from '../../store/reducers/authorizationReducer';
import { getActiveAuthorizationMode } from '../../store/selectors/authorizationSelectors';
import { CodeForm } from '../code-form/CodeForm';

export const AuthorizationModal = () => {
  const dispatch = useDispatch();
  const activeScreen = useSelector(getActiveAuthorizationMode);
  const handleNewScreen = (type) => dispatch(setActiveTab(type));

  return <div className={`authorization-modal is-${activeScreen}-active`}>
    <div className={'authorization-modal-content'}>
      <LoginForm />
      <div className="auth-toggle register-toggle" onClick={() => handleNewScreen('register')}><span>Register</span></div>
      <div className="auth-toggle sign-in-toggle" onClick={() => handleNewScreen('sign-in')}><span>Sign In</span></div>
      <RegistrationForm />
      <CodeForm />
    </div>
  </div>
}
