import { createAction, createReducer } from '@reduxjs/toolkit';

const initialState = {
  activeTab: 'sign-in',
  registrationFormErrors: {},
  registrationFormValidationInProgress: false,
};

export const validateUser = createAction('api/validate-user');
export const validateUserCode = createAction('api/validate-user-code');
export const validateCredentials = createAction('api/validate-credentials');
export const setActiveTab = createAction('auth/set-active-tab');
export const setRegistrationServerErrors = createAction('registration/set-registration-errors');
export const clearRegistrationServerErrors = createAction('registration/clear-registration-errors');
export const setRegistrationServerValidationStatues = createAction('registration/set-registration-validation');

export const authorizationReducer = createReducer(initialState, {
  [validateUser.type]: (state) => {
    return {...state, registrationFormValidationInProgress: true};
  },
  [setActiveTab.type]: (state, action) => {
    return {...state, activeTab: action.payload};
  },
  [setRegistrationServerErrors.type]: (state, action) => {
    return {...state, registrationFormErrors: action.payload};
  },
  [clearRegistrationServerErrors.type]: (state) => {
    return {...state, registrationFormErrors: {}};
  },
  [setRegistrationServerValidationStatues.type]: (state, action) => {
    return {...state, registrationFormValidationInProgress: action.payload};
  }
});
