import { Formik } from 'formik';
import * as Yup from 'yup';
import { validateUserCode } from '../../store/reducers/authorizationReducer';
import { useDispatch } from 'react-redux';

const codeValidationSchema = Yup.object().shape({
  code: Yup.string().required('Required'),
});

export const CodeForm = () => {
  const dispatch = useDispatch();
  const codeForm = {
    code: '',
  }

  const renderTextGroup = (name, error, label, type, handleChange) =>
    <div className={`form-group ${error && 'error-group'}`}>
      <label htmlFor={`code_${name}`}>
        {error ? <i className="fas fa-exclamation-triangle"><span className="error">{error}</span></i> : null }
        {label}
      </label>
      <input id={`code_${name}`} type={type} name={name} onChange={handleChange}/>
    </div>

  return <div className="auth-form full-size">
    <div className="form-header">
      <h1>Code confirmation</h1>
    </div>
    <p className="auth-info">
      We send the code to your email.
      Please enter it in the field below for confirmation.
    </p>

    <Formik
      initialValues={codeForm}
      validateOnChange={false}
      validateOnBlur={false}
      validationSchema={codeValidationSchema}
      onSubmit={(values, {setSubmitting}) => {
        dispatch(validateUserCode(values));
        setSubmitting(false);
      }}
    >
      {({
          errors,
          handleSubmit,
          handleChange,
        }) => (
        <form onSubmit={handleSubmit}>
          <input type="hidden" name="redirect-url"/>
          {renderTextGroup('code', errors.code, 'Code', 'text', handleChange)}
          <div className="form-group">
            <button type="submit">Confirm</button>
          </div>
        </form>
      )}
    </Formik></div>
}
