import './App.scss';
import Modal from '../modal/Modal';
import { useState } from 'react';
import { AuthorizationModal } from '../authorization-modal/AuthorizationModal';
import { useDispatch } from 'react-redux';
import { setActiveTab } from '../../store/reducers/authorizationReducer';

const App = () => {
  const [signInModalOpen, setSignInModalOpen] = useState(false);
  const dispatch = useDispatch();

  const openSignInModal = () => setSignInModalOpen(true);
  const closeSignInModal = () => {
    dispatch(setActiveTab('sign-in'));
    setSignInModalOpen(false);
  };

  return (
    <div className="main">
      <div className="background"/>
      <div className="login-container">
        <h1>Login / Sign Up<span className="application-name">Interview Platform</span></h1>
        <section className="social-links">
          <button
            id="manual-login"
            className="btn-link"
            title="Use login/password for authorization"
            onClick={openSignInModal}
          >
            <i className="fas fa-sign-in-alt" />
          </button>
          <a href="/ivwa/facebook" title="Facebook"><i className="fab fa-facebook-square" /></a>
          <a href="/ivwa/vk" title="VK"><i className="fab fa-vk"/></a>
          <a href="/ivwa/github" title="Github"><i className="fab fa-github-square" /></a>
        </section>
      </div>
      <Modal open={signInModalOpen} onClose={closeSignInModal}>
        <AuthorizationModal />
      </Modal>
    </div>
  );
}

export default App;
