import { ajax } from 'rxjs/ajax';
import { ofType } from 'redux-observable';
import { mergeMap, switchMap } from 'rxjs/operators';
import {
  clearRegistrationServerErrors,
  setActiveTab,
  setRegistrationServerErrors,
  validateCredentials,
  validateUser,
  validateUserCode,
} from '../reducers/authorizationReducer';

const validateUserEpic = action$ => action$.pipe(
  ofType(validateUser),
  mergeMap(action =>
    ajax.post(`/validate`, action.payload).pipe(
      switchMap(({response}) => {
        const actions = []
        if (response.status === 'success') {
          actions.push(
            clearRegistrationServerErrors(),
            setActiveTab('confirmation-code')
          );
        }
        if (response.status === 'validation-error') {
          actions.push(setRegistrationServerErrors(response.errors));
        }
        return actions;
      })
    )
  )
);

const validateUserCodeEpic = action$ => action$.pipe(
  ofType(validateUserCode),
  mergeMap(action =>
    ajax.post(`/register`, action.payload).pipe(
      switchMap(({response}) => {
        const actions = [];
        if (response.status === 'failed') {
          alert(response.error);
        }

        if (response.status === 'success') {
          window.location = response.url;
        }
        return actions;
      })
    )
  )
);

const validateCredentialsEpic = action$ => action$.pipe(
  ofType(validateCredentials),
  mergeMap(action =>
    ajax.post(`/sign-in`, action.payload).pipe(
      switchMap(({response}) => {
        const actions = [];
        if (response.status === 'failed') {
          alert(response.error);
        }

        if (response.status === 'success') {
          window.location = response.url;
        }
        return actions;
      })
    )
  )
);

export const registrationEpics = [validateUserEpic, validateUserCodeEpic, validateCredentialsEpic]
