import { Formik } from 'formik';
import * as Yup from 'yup';
import { useDispatch } from 'react-redux';
import { validateCredentials } from '../../store/reducers/authorizationReducer';

const loginValidationSchema = Yup.object().shape({
  email: Yup.string().email('Invalid email').required('Required'),
  password: Yup.string()
    .min(8, 'Password should include at least 8 characters')
    .max(16, 'Password should be no more than 16 characters')
    .required('Required'),
});

export const LoginForm = () => {
  const dispatch = useDispatch();
  const loginForm = {
    email: '',
    password: '',
  }

  const renderTextGroup = (name, error, label, type, handleChange) =>
    <div className={`form-group ${error && 'error-group'}`}>
      <label htmlFor={`register_${name}`}>
        {error ? <i className="fas fa-exclamation-triangle"><span className="error">{error}</span></i> : null }
        {label}
      </label>
      <input id={`register_${name}`} type={type} name={name} onChange={handleChange}/>
    </div>

  return <div className="auth-form sign-in">
    <div className="form-header">
      <h1>Account Sign In</h1>
    </div>

    <Formik
      initialValues={loginForm}
      validateOnChange={false}
      validateOnBlur={false}
      validationSchema={loginValidationSchema}
      onSubmit={(values, {setSubmitting}) => {
        setTimeout(() => {
          dispatch(validateCredentials(values));
          setSubmitting(false);
        }, 400);
      }}
    >
      {({
          errors,
          handleSubmit,
          handleChange,
        }) => (
        <form onSubmit={handleSubmit}>
          <input type="hidden" name="redirect-url"/>
          {renderTextGroup('email', errors.email, 'Email Address', 'text', handleChange)}
          {renderTextGroup('password', errors.password, 'Password', 'password', handleChange)}
          <div className="form-group">
            <button type="submit">Sign In</button>
          </div>
        </form>
      )}
    </Formik></div>
}
