import { Formik } from 'formik';
import * as Yup from 'yup';

import './RegistrationForm.scss';
import { useDispatch, useSelector } from 'react-redux';
import { validateUser } from '../../store/reducers/authorizationReducer';
import { getRegistrationErrors } from '../../store/selectors/authorizationSelectors';
import { useRef } from 'react';

const registrationValidationSchema = Yup.object().shape({
  firstName: Yup.string()
    .min(2, 'Too Short!')
    .max(20, 'Too Long!')
    .required('Required'),
  lastName: Yup.string()
    .min(2, 'Too Short!')
    .max(50, 'Too Long!')
    .required('Required'),
  email: Yup.string().email('Invalid email').required('Required'),
  password: Yup.string()
    .min(8, 'Password should include at least 8 characters')
    .max(16, 'Password should be no more than 16 characters')
    .required('Required'),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('password'), null], 'Passwords must match')
    .required(),
});

export const RegistrationForm = () => {
  const dispatch = useDispatch();
  const formikRef = useRef();
  const errors = useSelector(getRegistrationErrors);
  if (formikRef.current && Object.values(errors).length > 0) {
    formikRef.current.setErrors(errors);
  }

  const registrationForm = {
    email: '',
    firstName: '',
    lastName: '',
    confirmPassword: '',
    password: '',
  }

  const renderTextGroup = (name, values, error, label, type, handleChange) =>
    <div className={`form-group ${error && 'error-group'}`}>
      <label htmlFor={`register_${name}`}>
        {error ? <i className="fas fa-exclamation-triangle"><span className="error">{error}</span></i> : null }
        {label}
      </label>
      <input id={`register_${name}`} type={type} name={name} onChange={handleChange}/>
    </div>

  return <div className="auth-form register" ref={formikRef}>
    <div className="form-header">
      <h1>Account Registration</h1>
    </div>

    <Formik
      innerRef={formikRef}
      initialValues={registrationForm}
      validateOnChange={false}
      validateOnBlur={false}
      validationSchema={registrationValidationSchema}
      onSubmit={(values, {setSubmitting}) => {
        dispatch(validateUser(values));
        setSubmitting(false);
      }}
    >
      {({
          values,
          errors,
          handleSubmit,
          handleChange,
        }) => (
        <form onSubmit={handleSubmit}>
          <input type="hidden" name="redirect-url"/>
          {renderTextGroup('firstName', values.firstName, errors.firstName, 'First Name', 'text', handleChange)}
          {renderTextGroup('lastName', values.lastName, errors.lastName, 'Last Name', 'text', handleChange)}
          {renderTextGroup('email', values.email, errors.email, 'Email Address', 'text', handleChange)}
          {renderTextGroup('password', values.password, errors.password, 'Password', 'password', handleChange)}
          {renderTextGroup('confirmPassword', values.confirmPassword, errors.confirmPassword, 'Confirm Password', 'password', handleChange)}

          <div className="form-group">
            <button type="submit">Register</button>
          </div>
        </form>
      )}
    </Formik></div>
}
